(function(){
	'use strict';

	/**@ngInject **/
	function PropertyDetailController($log, $stateParams, PropertiesService){
	  var vm = this;

      vm.modelOptions = {
        getterSetter: true,
        allowInvalid: true,
        updateOn: 'default blur',
        debounce: { 
          default: 300,
          blur: 0
        }
      };

	  PropertiesService.getPropertyById($stateParams.id)
	  	.then(function(result){
			vm.property = result;
		});

	  vm.updateProperty = function(){
	  	$log.info('now property: ' + vm.property);
	  	PropertiesService.updateProperty(vm.property);
	  }
	  	


	}


	angular.module('thDemo')
	.controller('PropertyDetailController', PropertyDetailController)
})();
(function(){
	'use strict';

	/**@ngInject **/
	function PropertiesService($log, Property){
	  var service = this;

	  service.getProperties = function(){
	  	return Property.query();
	  }

	  service.createProperty = function(property) {
    	return new Property({
            address1: property.address1,
            address2: property.address2,
            city: property.city, 
            state: property.state,
            zip: property.zip,
            kind: property.kind
          }).create();
  		}	

  	  service.getPropertyById = function(id){
  	  	return Property.get(id);
  	  }		

      service.updateProperty = function(property){
        return Property.get(property.id)
          .then(function(result){
            result.address1 = property.address1,
            result.address2 = property.address2,
            result.city = property.city, 
            result.state = property.state,
            result.zip = property.zip,
            result.kind = property.kind
            result.update();
          });
      }   

  	  service.deleteProperty = function(id){
        return service.getPropertyById(id)
          .then(function(result){
            result.delete();
          })
  	  }	

	}


	angular.module('thDemo')
	.service('PropertiesService', PropertiesService)
})();
(function(){
	'use strict';

	/**@ngInject **/
	function PropertiesController($log, $filter, $state, $uibModal, PropertiesService){
	  var vm = this;

      vm.modelOptions = {
        getterSetter: true,
        allowInvalid: true,
        updateOn: 'default blur',
        debounce: { 
          default: 300,
          blur: 0
        }
      };

	  PropertiesService.getProperties()
	  	.then(function(result){
			vm.properties = result;
		});

	  	vm.deleteProperty = function(property){
	  	  var modalInstance = $uibModal.open({
          	controller: "DeletePropertyConfController as vm",
          	templateUrl: 'app/properties/delete-property-conf.html',
          	size: 'lg',
          	resolve: {
            	prop: function () {
              	  return {
              	  	id: property.id,
              	  	address1: property.address1
              	  };
            	}
	        }
	      });

	  	  modalInstance.result
            .then(function() {
              $state.reload();
            }, function(err) {
             $log.info(err);
            });
	  	}
	}

	angular.module('thDemo')
	.controller('PropertiesController', PropertiesController)
})();
(function(){
	'use strict';

	/**@ngInject **/
	function DeletePropertyConfController($log, $uibModalInstance, prop, PropertiesService){
	  var vm = this;
	  vm.prop = prop;

      vm.cancel = function() {
      	$uibModalInstance.dismiss('cancel');
      };

	  vm.deleteProperty = function(id){
	  	return PropertiesService.deleteProperty(id)
	  	  .then(function(result){
	  	  	$uibModalInstance.close(result);
	  	  });
	  }
		
	}


	angular.module('thDemo')
	.controller('DeletePropertyConfController', DeletePropertyConfController)
})();
(function() {
  'use strict';

  /** @ngInject */
  function MainController($log, Property, PropertiesService, toastr) {
    var vm = this;

    vm.validate = function(p){
      if(!p || !p.address1 || !p.city || !p.state || !p.zip || !p.kind ){
        return false; 
      } else {
        return p; 
      }
    }

     vm.propertyKindOptions= [
        'Apartment',
        'Cabin',
        'Condo',
        'Single Family House',
        'Townhouse'
      ]

    vm.createProperty = function(property) {
      if(vm.validate(property)){
       PropertiesService.createProperty(property)
         .then(function(response){
          if(response){
            $log.info(response);
            toastr.success('Property Created!', 'Success!');
          } else {
            toastr.error('Something went wrong!', 'Error!');
          }
        })
      } else {
        toastr.error('There are invalid form fields!', 'Error!');
      }
    };
  }

  angular
    .module('thDemo')
    .controller('MainController', MainController);

})();

